.image-gallery {
  display: flex;
  flex-wrap: wrap;
  padding: 6px;
  margin-top: 295px;
  background-color: rgba(255, 255, 255, 0.137);
  -webkit-backdrop-filter: blur(6px);
          backdrop-filter: blur(6px);
}

li {
  height: 50vh;
  flex-grow: 1;
  list-style-type: none;
  border-radius: 12px;
  padding: 6px;
}

li img {
  max-height: 100%;
  min-width: 100%;
  object-fit: cover;
  vertical-align: bottom;
  border-radius: 12px;
}
@media (max-width: 480px) {
  .image-gallery {
    display: grid;
    grid-template-columns: 1fr;
  }

  li {
    height: auto;
    width: calc(100% - 12px);
    flex-grow: 1;
    list-style-type: none;
    border-radius: 12px;
    padding: 6px;
  }

  li img {
    /* max-height: 100%;
  min-width: 100%; */
    width: 100%;
    object-fit: cover;
    vertical-align: bottom;
    border-radius: 12px;
  }
}

