body {
  background-color: var(--bg);
  /* padding: 12px; */
}
.App {
  text-align: center;
}
.App header h2 {
  font-size: 2rem;
  font-weight: bold;
  font-family: "Reggae One", cursive;
  margin-top: 5rem;
}

.filters {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  margin: 3rem 0;
}
.filters button {
  /* background-color: red; */
  border-radius: 6px;
  font-family: "Josefin Sans", sans-serif;
  padding: 8px 16px;
  font-size: 1rem;
  background-color: rgb(23, 24, 27);
  color: white;
  border: none;
  text-transform: capitalize;
}
.top-view {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: -1;
}

